import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class PostService {

  constructor(private http: HttpClient) { }

  public createPost(sendDatas: {user_id: string, message: string, date: string}): Observable<string> {
    return this.http.post<string>('/request/post/create', sendDatas);
  }
}
